import React from "react";

function References() {
  return (
    <div className="p-10 min-h-screen text-white bg-gradient-to-br from-indigo-900 to-purple-900">
      <h1 className="text-4xl font-bold mb-6 text-center">📚 Sensor References</h1>

      <div className="space-y-6 text-lg max-w-3xl mx-auto">
        <div className="bg-white bg-opacity-10 p-6 rounded-xl shadow-md">
          <h2 className="text-2xl font-semibold mb-2">🔆 LDR (Light Dependent Resistor)</h2>
          <p>
            Used to detect ambient light intensity. It helps the system respond to light changes.
          </p>
          <a
            href="https://en.wikipedia.org/wiki/Photoresistor"
            target="_blank"
            rel="noreferrer"
            className="underline text-blue-300 mt-2 inline-block"
          >
            🔗 Learn more about LDR
          </a>
        </div>

        <div className="bg-white bg-opacity-10 p-6 rounded-xl shadow-md">
          <h2 className="text-2xl font-semibold mb-2">💨 MQ2 Smoke Sensor</h2>
          <p>
            Detects smoke and flammable gases. It's critical for identifying fire hazards.
          </p>
          <a
            href="https://components101.com/sensors/mq2-gas-sensor"
            target="_blank"
            rel="noreferrer"
            className="underline text-blue-300 mt-2 inline-block"
          >
            🔗 Learn more about MQ2
          </a>
        </div>

        <div className="bg-white bg-opacity-10 p-6 rounded-xl shadow-md">
          <h2 className="text-2xl font-semibold mb-2">🌡️ DHT11 Temperature Sensor</h2>
          <p>
            Measures temperature and humidity. Important for monitoring environmental changes.
          </p>
          <a
            href="https://components101.com/sensors/dht11-temperature-sensor"
            target="_blank"
            rel="noreferrer"
            className="underline text-blue-300 mt-2 inline-block"
          >
            🔗 Learn more about DHT11
          </a>
        </div>
      </div>
    </div>
  );
}

export default References;
